/* Стили для подкастов */
.podcasts {
    display: flex;
    flex-direction: column;
    margin: 2rem;
    padding: 20px;
    background-color: var(--color-result-bg);
    color: var(--color-white);
    border-radius: 20px;
    box-shadow: 0 4px 8px var( --color-shadow);
}

.podcast-list {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    width: 100%;
}
.podcast {
    width: calc(50% - 10px);
    background-color: #4c4945;
    padding: 15px;
    margin-bottom: 20px;
    border-radius: 15px;
    box-shadow: 0 2px 4px var(--color-shadow);
}
.podcast-title {
    font-family: 'TITLE', sans-serif;
    font-size: 30px;
    text-align: center;
}


.podcast button {
    padding: 10px 15px;
    background-color: var(--color-button-bg);
    color: var(--color-text-alt);
    border: none;
    border-radius: 10px;
    cursor: pointer;
    transition: all 0.3s ease;
}

.podcast button:hover {
    background-color: var(--color-button-hover);
    color: var(--color-bg-alt);
}

/*=== podcasts.css ===*/
@media (max-width: 768px) {
    .podcast-list {
        flex-direction: column;   
        width: 90%;
    }
    .podcast {
        width: 90% !important;
    }

    .podcast_audio {
        width: 90%;
    }
    .podcast-title {
        font-size: 1rem;
    }
}