.App {
    display: flex;
    flex-direction: column;
    background-size: cover;
    background-repeat: no-repeat;
    background-position: center center;
    background-attachment: fixed;
    text-shadow: 1px 1px 2px rgba(0, 0, 0, 0.7);
}
.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}


* {
  list-style: none;
  text-decoration: none;
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
}

/*===== Сброс стилей =====*/
html {
  scroll-behavior: smooth;
}
body, html {
  height: 100%;
  margin: 0;
  padding: 0;
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
  color: #e0e0e0;
}
body {
  display: flex;
  flex-direction: column;
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center center;
  background-attachment: fixed;
  background-image: url("/public/assets/photo/background`s/background.webp");
  text-shadow: 1px 1px 2px rgba(0,0,0,0.7);
}
body::before {
  content: "";
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: var(--color-before);
  z-index: -1;
}
.container {
  align-items: center;
}
/*===== Основной блок =====*/ 
main {
  flex: 1;
  transition: padding-left 0.3s ease;
}
section {
  margin-bottom: 20px;
}
section h2 {
  font-weight: bold; 
}

.section {
  display: none;
}
.section.active {
  display: contents; 
}

/*===== Button =====*/
button {
  background-color: var(--color-button-bg);
  border: none;
  color: var(--color-text-alt);
  font-size: 1.5rem;
  padding: 0.8rem 1.5rem;
  cursor: pointer;
  margin: 10px;
  border-radius: 30px;
  transition: background-color 0.3s, transform 0.3s, box-shadow 0.3s;
  font-family: 'Times New Roman', Times, serif;
  font-weight: bold;
}
button:hover {
  background-color: var(--color-button-hover);
  color: var(--color-hover-text);
  transform: translateY(-3px);
  box-shadow: 0 4px 6px var(--color-shadow);
}
button:active {
  transform: translateY(1px);
  box-shadow: 0 2px 4px var(--color-shadow);
}

.articles .article button {
  max-width: 200px;
  margin: 10px auto;
  display: block;
}


@media (max-width: 600px) {
  .container {
      padding: 0px;
  }


  /* body */
  body {
      background-size: cover;
      background-repeat: no-repeat;
      background-position: center center;
      background-attachment: fixed; 
      background-image: url("/public/assets/photo/background`s/background-Mobile.webp");
      font-size: 14px; 
      position: relative;
      z-index: 1; 
  }

  body::before {
      content: "";
      position: fixed;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      background: var(--color-before);
      z-index: -1;
  }


  /* main */
  main {
      padding-top: 10px;
      padding-bottom: 30px;
  }

  /* section */
  section {
      margin-bottom: 15px;
  }

  /* menu */
  .menu {
      border-right: 15px solid #3c3c3c;
  }
}

@media (max-width: 768px) {
  * {
      list-style: none;
      text-decoration: none;
      box-sizing: content-box;
  }
  button {
    font-size: 1rem;
  }
  /* content */
  #content.shift {
      margin-left: 0; 
  }

  /* menu-icon */
  .menu-icon {
      display: block;
  }

  /* menu */
  .menu {
      left: -310px;
  }
  .menu:hover {
      left: 0;
  }
}

/* message */
@media (max-width: 768px) {
  .message.container {
      flex-direction: column;
      align-items: center;
  }

  #results div, .event, #error-message {
      padding: 10px;
      margin: 5px 0;
      font-size: 14px;
      border-radius: 10px;
  }

  #error-message {
      display: flex;
      position: inherit;
      width: 280px;
  }
}