.modal {
    display: none;
    position: fixed;
    z-index: 1;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    overflow: auto;
    background-color: rgba(0, 0, 0, 0.6);
    padding-top: 60px;
    transition: opacity 0.3s ease-in-out;
}

.modal-content {
    font-family: 'Times New Roman', Times, serif;
    font-size: 16px;
    line-height: 1.6;
    background-color: rgba(0, 0, 0, 0.8);
    margin: 5% auto;
    padding: 20px;
    border: 1px solid #888;
    width: 80%;
    max-width: 600px;
    border-radius: 10px;
    box-shadow: 0 4px 8px var(--color-shadow);
    position: relative;
    animation: modalOpen 0.5s ease;
    overflow: auto;
}

.modal-content-wrapper {
    display: flex;
    flex-direction: column;
    flex-wrap: wrap;
    align-items: center;
}
.modal-content {
    background-color: rgba(0, 0, 0, 0.7);
}

.modal-image {
    width: 100%;
    max-width: 500px;
    height: auto;
    border-radius: 10px;
}
.modal-text {
    flex: 1;
    text-align: center;
    overflow-y: auto;
}

/* Стили для кнопки закрытия */
.close-modal-button {
    background-color: #4b2a2a;
    border: 2px solid #8b5e5e;
    color: #f1e1c6;
    font-size: 1.5rem;
    padding: 1rem 2rem;
    cursor: pointer;
    margin: 20px auto;
    display: block;
    border-radius: 20px;
    font-family: 'Georgia', serif;
    font-weight: bold;
    text-transform: uppercase;
    box-shadow: 0 4px 10px rgba(0, 0, 0, 0.4);
    transition: all 0.3s ease;
}

.close-modal-button:hover {
    background-color: #6e3b3b;
    border-color: #f1e1c6;
    color: #fff;
    transform: translateY(-3px);
    box-shadow: 0 6px 12px rgba(0, 0, 0, 0.5);
}

.close-modal-button:active {
    transform: translateY(2px);
    box-shadow: 0 2px 6px rgba(0, 0, 0, 0.4);
}




/* Стили для текста и заголовков */
.modal-title {
    font-family: 'TITLE', sans-serif;
    font-size: 30px;
    text-align: center;
    color: var(--color-white);
}

#modal-description {
    color: var(--color-white);
}

@media (max-width : 768px) {
    .modal-content {
        padding-top: 50px;
    }
}