/*=== Дотс Навигация ===*/
.dots-container {
    display: flex;
    justify-content: center;
    margin-top: 1rem;
}

.dot {
    width: 10px;
    height: 10px;
    background-color: #ccc;
    border-radius: 50%;
    margin: 0 5px;
    cursor: pointer;
    transition: background-color 0.3s;
}

.dot.active {
    background-color: #fff;
}

.dot:hover {
    background-color: #bbb;
}

/*=== Адаптация ===*/
@media (max-width: 600px) {
    /* Галерея */
    .gallery {
        padding: 1rem;
    }

    .gallery h2 {
        font-size: 24px;
    }

    /* Слайдер */
    .slider {
        position: relative;
        width: 100%;
        overflow: hidden;
    }

    .slider-wrapper {
        display: flex;
        flex-direction: column;
        transition: transform 0.5s ease;
        will-change: transform;
    }

    .collage {
        display: flex;
        flex-direction: column;
        gap: 1rem;
        width: 100%;
        overflow-y: auto;
    }

    .collage-photo {
        position: relative;
        overflow: hidden;
        border-radius: 10px;
        aspect-ratio: 1 / 1;
        max-width: 100%;
        width: 100%;
        transition: transform 0.5s ease;
    }

    .photo-description {
        visibility: hidden;
        opacity: 0;
        position: absolute;
        bottom: 10px;
        left: 10px;
        right: 10px;
        background: rgba(0, 0, 0, 0.7);
        color: var(--color-white);
        padding: 1rem;
        border-radius: 10px;
        text-align: left;
        transition: visibility 0s, opacity 0.5s ease;
        transform: translateY(20px);
    }

    .collage-photo:hover .photo-description {
        visibility: visible;
        opacity: 1;
        transform: translateY(0);
    }

    .photo-description h3 {
        font-weight: bold;
        margin-bottom: 0.5rem;
    }

    .photo-description p {
        line-height: 1.4;
        margin-bottom: 0;
    }


    /* Кнопки */
    .scroll-controls, .dots-container {
        display: none;
    }
}
